 
  .bgg {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }
    
  .snowflake{
    position: absolute;
    font-size: 2rem;
    color: #fff;
    height: 100%;
    animation: snow infinite;
  }
  
  .snow {
      position: absolute;
      z-index: 2;
      width: 459px;
      height: 0;
      background-color: #fff;
      bottom: 0;
      animation: windowSnow 10s ease-in infinite forwards;
  }
  
